import React, { Component, useState } from 'react'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'

export default class Plan extends Component {
  constructor(props) {
    super(props)
    this.state = {
      plans: [], //
      selectdate: new Date(), //Plan
      addplanText: '', //Plan
    }
  }
  componentDidMount() {
    fetch('userdata')
      //fetch('testuserdata')
      .then(res => res.json())
      .then(data => {
        if (data.administer) {
          //
          this.loadPlans()
        } else {
          //
          alert('')
          this.props.history.push('/')
        }
      })
  }
  loadPlans = () => {
    fetch('plandata')
      //fetch('testplandata')
      .then(res => res.json())
      .then(data => this.setPlans(data))
  }
  //Planlist
  setPlans = plans => {
    const list = []
    for (let plan of plans) {
      list[plan.date] = { text: plan.text, date: plan.date }
    }
    this.setState({
      plans: list,
    })
  }
  //Date
  onChangeSelectDate = date => {
    this.setState({
      selectdate: date,
    })
  }
  //Text
  onChangeAddText = e => {
    this.setState({
      addplanText: e.target.value,
    })
  }
  //
  onClickAddPlan = () => {
    //
    if (this.state.addplanText === '') {
      alert('')
      return
    }
    const adddate = this.getFormatDate(this.state.selectdate)
    const data = {
      text: this.state.addplanText,
      date: adddate,
    }
    //
    if (this.state.plans[adddate]) {
      //
      if (
        window.confirm(
          '\n' +
            '' +
            this.state.plans[adddate].text +
            '',
        )
      ) {
        //
        fetch('/updateaddplandata', {
          //fetch('/testupdateaddplandata', {
          method: 'POST',
          body: JSON.stringify(data),
          headers: {
            'Content-Type': 'application/json',
          },
          mode: 'cors',
        })
          .then(res => res.json())
          .then(str => {
            alert('')
            this.loadPlans()
          })
      }
      return
    }
    //
    //fetch
    fetch('/addplandata', {
      //fetch('/testaddplandata', {
      method: 'POST',
      body: JSON.stringify(data),
      headers: {
        'Content-Type': 'application/json',
      },
      mode: 'cors',
    })
      .then(res => res.json())
      .then(str => {
        alert('')
        this.loadPlans()
      })
  }
  onClickDeletePlan = date => {
    if (
      window.confirm(
        '\n' + this.state.plans[date].text + '',
      )
    ) {
      //
      fetch('/deleteplandata', {
        //fetch('/testdeleteplandata', {
        method: 'POST',
        body: JSON.stringify([date]),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(res => res.json())
        .then(str => {
          this.loadPlans()
        })
    }
  }
  //yyyyMMdd
  getFormatDate(date) {
    return `${date.getFullYear()}${('0' + (date.getMonth() + 1)).slice(-2)}${(
      '0' + date.getDate()
    ).slice(-2)}`
  }
  render() {
    return (
      <div>
        <h1>Plan</h1>
        <div className="flame" id="plan-addflame">
          <p id="addplanTitle"></p>
          <span id="namesubtitle"></span>
          <div>
            
            <SimpleDatePicker
              onChange={date => this.onChangeSelectDate(date)}
            />
          </div>
          <div>
            <input
              placeholder="()"
              maxLength="4"
              className="plan-input"
              value={this.state.addplanText}
              onChange={this.onChangeAddText}
            />
          </div>
          <button
            className="bluebutton"
            id="plan-addbutton"
            onClick={this.onClickAddPlan}
          >
            
          </button>
        </div>
        <div>
          <span></span>
          <span></span>
        </div>
        <div>
          <table>
            <thead>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
            </thead>
            <tbody>
              {this.state.plans.map(plan => (
                <tr key={plan.date}>
                  <td>{plan.date}</td>
                  <td>{plan.text}</td>
                  <td>
                    <button
                      className="redbutton"
                      onClick={() => this.onClickDeletePlan(plan.date)}
                    >
                      
                    </button>
                  </td>
                  <td>
                    <button className="bluebutton"></button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    )
  }
}
const SimpleDatePicker = props => {
  const initialDate = new Date()
  const [startDate, setStartDate] = useState(initialDate)
  const handleChange = date => {
    setStartDate(date)
    props.onChange(date)
  }

  return (
    <DatePicker
      dateFormat="yyyy/MM/dd"
      selected={startDate}
      onChange={handleChange}
      customInput={
        <button className="datepicker-button">{dateFormater(startDate)}</button>
      }
    />
  )
}
const dateFormater = date => {
  return date.getFullYear() + '/' + (date.getMonth() + 1) + '/' + date.getDate()
}
